@import "https://fonts.googleapis.com/css2?family=Inter&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  ---main-color: #7744d9;
  ---global-text-color: #7a7a7a;
}

body {
  font-family: Inter, sans-serif;
}

.global-text {
  color: var(---global-text-color);
}

.main-h {
  font-size: 36px;
}

.em-bar {
  background: #aec6ef;
  border-radius: 30px;
  width: 90px;
  height: 5px;
  margin: 20px auto;
  position: relative;
}

.em-bar:before {
  content: "";
  animation-name: MOVE-BG;
  -webkit-animation-duration: 3s;
  background: #7744d9;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  animation-duration: 6s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: -2.7px;
  left: 0;
}

@keyframes MOVE-BG {
  0% {
    left: 0;
  }

  50% {
    left: 100%;
  }

  100% {
    right: 100%;
  }
}

.hero-bg {
  background-position: center;
  background-size: cover;
  place-items: center;
  height: 550px;
  display: grid;
  position: relative;
}

.hero-bg .container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.hero-bg .text-wrapper {
  position: relative;
}

.hero-bg .text-wrapper h2 {
  color: #fff;
  text-align: center;
  font-size: 31px;
  font-weight: bold;
}

.hero-bg .text-wrapper ul {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.hero-bg .text-wrapper ul li {
  color: #fff;
  text-transform: uppercase;
  margin: 0 6px;
}

.hero-bg:before {
  content: "";
  background-color: #29345c9e;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.tab-btns {
  gap: 20px;
  display: flex;
}

.tab-btn {
  color: #000;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  padding: 4px 20px;
  font-weight: bold;
}

@media screen and (width <= 767px) {
  .nav-link {
    color: #000;
  }

  .hero-bg {
    height: 400px;
  }

  .hero-bg img {
    background-size: cover;
  }

  .hero-bg:before {
    background-color: #29345ccc;
  }
}

.abt-company {
  margin-top: 150px;
  margin-bottom: 20px;
}

.abt-col h5 {
  color: #7744d9;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 10px;
  font-size: 15px;
