}

.client {
  background-color: #271151;
  margin-top: 100px;
}

.client-col {
  border: 1px solid #eee;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  padding: 30px 0;
  transition: all .3s;
  display: flex;
  position: relative;
}

.client-col .client-icon {
  position: absolute;
