}

.why-choose {
  margin-top: 100px;
}

.why-choose .container h5 {
  color: #7744d9;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  margin-bottom: 10px;
  font-size: 15px;
  font-weight: 600;
}

.why-choose .container h1 {
  text-transform: capitalize;
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 33px;
  font-weight: 600;
  line-height: 1.2;
}

.why-col {
  text-align: left;
  background: #fff;
  border: 2px dotted #c2a9f0;
  margin-bottom: 30px;
  padding: 30px 30px 28px;
  transition: all .5s;
  animation: 5s linear infinite animated-border;
  position: relative;
}

@keyframes animated-border {
  0% {
    border-color: #c2a9f0;
  }

  25% {
    border-color: #f0a9c2;
  }

  50% {
    border-color: #a9f0c2;
  }

  75% {
    border-color: #f0c2a9;
  }

  100% {
    border-color: #c2a9f0;
  }
}

.why-box-icon {
  color: #333;
  -o-transition: all .5s ease 0s;
  float: left;
  margin-right: 35px;
  font-size: 35px;
  transition: all .5s;
  display: block;
  position: relative;
}

.why-box-icon h2 {
  text-align: center;
  color: #c216db;
  background-color: #c2a9f0;
  border: 2px dotted #fff;
  border-radius: 50%;
  width: 90px;
  height: 90px;
  font-size: 30px;
  font-weight: 600;
  line-height: 88px;
  transition: all .5s;
  animation: 7s linear infinite alternate forwards border-transform;
  display: inline-block;
  position: relative;
}

@keyframes border-transform {
  0%, 100% {
    border-radius: 63% 37% 54% 46% / 55% 48% 52% 45%;
  }

  14% {
    border-radius: 40% 60% 54% 46% / 49% 60% 40% 51%;
  }

  28% {
    border-radius: 54% 46% 38% 62% / 49% 70% 30% 51%;
  }

  42% {
    border-radius: 61% 39% 55% 45% / 61% 38% 62% 39%;
  }

  56% {
    border-radius: 61% 39% 67% 33% / 70% 50% 50% 30%;
  }

  70% {
    border-radius: 50% 50% 34% 66% / 56% 68% 32% 44%;
  }

  84% {
    border-radius: 46% 54% 50% 50% / 35% 61% 39% 65%;
  }
}

.why-box-icon .why-box-number span i {
  color: #7744d9;
  text-align: center;
  background: #fff;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  margin-bottom: 20px;
  font-size: 24px;
  line-height: 50px;
  display: inline-block;
  position: absolute;
  top: 62px;
  left: 56px;
  box-shadow: 0 0 20px 3px #0000000d;
}

.why-col-content h2 {
  margin: 0 0 12px;
  font-size: 22px;
  font-weight: bold;
  transition: all .5s;
}

.why-col-content .description p {
  font-size: 15px;
}

.why-col-content .description a {
  color: #7744d9;
  text-transform: uppercase;
  margin-top: 10px;
  font-size: 14px;
  text-decoration: none;
  display: inline-block;
}

@media screen and (width <= 500px) {
  .why-choose .container h1 {
    text-transform: capitalize;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 24px;
    font-weight: 600;
    line-height: 1.2;
  }
}

